<script>
import Loader from "../components/Loading.vue";
import { getUser, addUser, checkUsername } from "../api/User.js";
export default {
  components: {
    Loader
  },
  data() {
    return {
      isLoadingShow: false,
      isLogin: true,
      username: "",
      password: "",
      password2: "",
      register: {
        username: "",
        fullname: "",
        email: "",
        password: "",
        search_count: 0
      }
    };
  },
  methods: {
    signUp() {
      if (this.register.username.length < 5) {
        this.$Message["warning"]({
          background: true,
          content: "Tn ng nhp khng th b hn 5 k t"
        });
      } else if (this.register.password.length < 8) {
        this.$Message["warning"]({
          background: true,
          content: "Mt khu ti thiu l 8 k t"
        });
      } else if (this.password2 != this.register.password) {
        this.$Message["warning"]({
          background: true,
          content: "Mt khu nhp li khng khp"
        });
      } else if (this.register.email.length < 10) {
        this.$Message["warning"]({
          background: true,
          content: "Email khng hp l"
        });
      } else {
        this.isLoadingShow = true;
        checkUsername(this.register.username)
          .then(ra => {
            if (ra.data === 0) {
              this.isLoadingShow = true;
              addUser(this.register)
                .then(r => {
                  if (r.data == 1) {
                    this.username = this.register.username;
                    this.password = this.register.password;
                    this.login();
                  } else {
                    this.$Message["error"]({
                      background: true,
                      content: "ng k khng thnh cng, vui lng th li"
                    });
                  }
                })
                .catch(e => {
                  this.$Message["error"]({
                    background: true,
                    content: "ng k khng thnh cng, vui lng th li"
                  });
                })
                .finally(() => {
                  // this.isLoadingShow = false;
                });
            } else {
              this.$Message["error"]({
                background: true,
                content: "Tn ng nhp  c s dng"
              });
            }
          })
          .catch(e2 => {
            this.$Message["error"]({
              background: true,
              content: " xy ra li, vui lng th li"
            });
          })
          .finally(() => {
            this.isLoadingShow = false;
          });
      }
    },
    login() {
      if (this.username.length < 5 || this.password < 8) {
        this.$Message["error"]({
          background: true,
          content: "Vui lng nhp y  thng tin"
        });
      } else {
        this.isLoadingShow = true;
        getUser(this.username, this.password)
          .then(res => {
            if (res.data.id != undefined && res.data.id > 0) {
              var userObj = res.data;
              // ng nhp thnh cng, x l  y
              this.$Message["success"]({
                background: true,
                content: "Xin cho " + userObj.fullname + "!"
              });
              this.$session.start();
              this.$session.set("USER_INFO", userObj);
              this.$router.push("/profile");
            } else {
              this.$Message["error"]({
                background: true,
                content: "ng nhp tht bi, vui lng th li " + r.data
              });
            }
          })
          .catch(e => {
            this.$Message["error"]({
              background: true,
              content: "ng nhp tht bi, vui lng th li " + e.message
            });
          })
          .finally(() => {
            this.isLoadingShow = false;
            this.password = "";
          });
      }
    }
  }
};
</script>
